          ]
        },
        {
          path: "./3",
          devTop: "2200px",
          devLeft: "1570px",
          devSize: this.goldSize,
          top: "1600px",
          left: "1670px",
          size: "450px",
          numPics: 3,
          shadowVisible: false,
          activeIndex: 0,
          description: [
